
#timeline {
  z-index: 1;
  margin: auto; 
  width: 30%;
  position: absolute;
  margin-left: 20%;
}
#main {
  display: block; 
  width: 100%;
  height: 100%;
  background: #fdbfcb;
  position: relative;
  left: 0;
}
#main img {
  display: block;
  width: 100%;
  height: 100%;
}
#banner {
  display: block; 
  background: #fdbfcb;
  width: 100%;
  top: 0;
  left: 0;
}
#banner img {
  width: 15%;
  height: 100%;
}



